body.ieBrowser .blur-bg{
	filter: none !important; 
	-ms-filter: none !important; 
	-webkit-filter: none !important; 
	opacity:0.15 !important;
}
.themeSquarePhoto .flatCard {height:300px;}
.themeSquarePhoto .flatCard .post, .themeSquarePhoto .flatCard .post .image, .themeSquarePhoto .flatCard .postContent{height:100%;}
.themeSquarePhoto .flatCard .img-responsive{
	visibility: hidden !Important; 
	position: absolute;
	top: 50%;
	left:50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}
.themeSquarePhoto .flatCard .image .blur-bg{
    position: static;
    width: inherit;
    height: inherit;
    filter: blur(0px);
    -webkit-filter: blur(0px); 
    -ms-filter: blur(0px);
	-moz-filter: blur(0px);	
    background-size: cover !important;
    background-position: center center !important;
    opacity: 1;
    object-fit: cover;
    width: 100%;
    object-position: center;
}
body.ieBrowser .themeSquarePhoto .flatCard .image .blur-bg{opacity:1 !Important;}
.themeSquarePhoto .flatCard .postContentCard{
    border:0px; 
    position:absolute; 
    left:0px; 
    top:0px; 
    width:100%; 
    height:100%;
    opacity: 0;
    background-color: rgba(0,0,0,0.7);
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;    
    
}
.themeSquarePhoto .flatCard .post:hover .postContentCard{opacity: 1;}
.themeSquarePhoto .flatCard .postContentCard .postedTxt{
    position: absolute;
    top: 50%;
    transform: translateY(-55%);
    -ms-transform: translateY(-55%);
    -webkit-transform: translateY(-55%); 
    left: 0;    
    right: 0;
    color:#fff;
    text-align: center;
    padding: 0 10px;
}

.themeSquarePhoto .square-text-trim p{
font-size: 16px;
}
.themeSquarePhoto .flatCard .postNetwork{
    position: absolute;
    bottom: 20px; right:10px; z-index:100;
}
.themeSquarePhoto .flatCard .postNetwork i{ 
    font-size: 24px; line-height: 18px; color:#fff;
}
.themeSquarePhoto .flatCard h4 .authorDetails{
    position: absolute;
    bottom: 10px;
    width: 94%; 
}
.themeSquarePhoto .Columns-1{
    width: 20px!important;
}
.themeSquarePhoto .Columns-2{
    width: 40px!important;
}
.themeStart4 .flatCard .image {
    height: 250px
}
.themeSquarePhoto .flatCard .postNetwork svg {
    margin-bottom: -4px!important
}
.theme4_poweredBy {
    right: 13px;
    top: -40px
}
.theme4_poweredByMain {
    padding-top: 0;
    display: block!important
}
.flatCard .postedTxt p {
    white-space: normal;
    overflow: hidden;
    display: -webkit-box;
    /* -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis; */
    width: 100%;
}

