        border-radius: 0;
        -moz-border-radius: 0;
        -webkit-border-radius: 0;
        box-shadow: none;
        -moz-box-shadow:none;
        -webkit-box-shadow:none;
        -moz-transition: all ease 200ms;
        -webkit-transition: all ease 200ms;
        transition: all ease 200ms; 
    }
    .single_widget_theme .shareWrapper:hover a {
        transform:none;
        -webkit-transform:none;
        -moz-transform:none;
    }
    
