html {
    scroll-behavior: smooth;
    scrollbar-width: none;
}
/* body.wPostFull {
    overflow: scroll
} */
body.wPostFull, body.wPostFull #wloading {
    background: 0 0!important
}
.clear, .clearfix, .clr {
    clear: both
}
/* CTA CSS Start */
.cardCTA a {
    width: 100%;
    text-align: center;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
    background: #333;
    color: #fff;
    padding: 8px 0 8px 8px;
    display: block;
    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}
.cardCTA a:focus, .cardCTA a:hover {
    text-decoration: none
}
/* CTA CSS End */
/* Share Icons CSS Start */
.shareWrapper {
    position: absolute;
    top: -15px;
    left: 15px;
    opacity: 0;
    visibility: hidden;
    -webkit-backface-visibility: hidden;
    -moz-transition: all .1s ease 0s;
    -webkit-transition: all .1s ease 0s;
    transition: all .1s ease 0s;
    z-index: 1400
}
.postContent:hover .shareWrapper {
    opacity: 1;
    visibility: visible;
    top: 10px
}
.shareWrapper a {
    display: block!important;
    background: #fff;
    font-size: 16px!important;
    width: 30px;
    height: 30px;
    line-height: 30px!important;
    text-align: center;
    position: relative;
    border-radius: 30px;
    -moz-border-radius: 30px;
    -webkit-border-radius: 30px;
    box-shadow: 0 1px 3px rgba(169, 167, 167, .5);
    -moz-box-shadow: 0 1px 3px rgba(169, 167, 167, .5);
    -webkit-box-shadow: 0 1px 3px rgba(169, 167, 167, .5);
    -moz-transition: all ease .2s;
    -webkit-transition: all ease .2s;
    transition: all ease .2s;
    color: #333
}
.shareWrapper a {
    position: absolute;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: all ease .3s
}
.shareWrapper a:first-child {
    z-index: -1
}
.shareWrapper a:nth-child(2) {
    z-index: -2
}
.shareWrapper a:nth-child(3) {
    z-index: -3
}
.shareWrapper a:nth-child(4) {
    z-index: -4
}
.shareWrapper .fbShare i.fa-facebook {
    color: #3b5998!important
}
.shareWrapper .twShare i.fa-twitter {
    color: #55acee!important;
    line-height:30px;
}
.shareWrapper .gpShare i {
    color: #e04006!important
}
.shareWrapper .liShare i {
    color: #0177b5!important;
     line-height: 30px;
}
.shareWrapper .fbShare i {
     line-height: 30px;
}
.shareWrapper:hover a {
    opacity: 1;
    position: relative!important
}
.shareWrapper:hover a:first-child {
    z-index: 1
}
.shareWrapper:hover a:nth-child(2) {
    -webkit-transform: translate3d(0, 10px, 0);
    -moz-transform: translate3d(0, 10px, 0);
    -ms-transform: translate3d(0, 10px, 0);
    transform: translate3d(0, 10px, 0);
    left: 0
}
.shareWrapper:hover a:nth-child(3) {
    -webkit-transform: translate3d(0, 20px, 0);
    -moz-transform: translate3d(0, 20px, 0);
    -ms-transform: translate3d(0, 20px, 0);
    transform: translate3d(0, 20px, 0);
    left: 0
}
.shareWrapper:hover a:nth-child(4) {
    -webkit-transform: translate3d(0, 30px, 0);
    -moz-transform: translate3d(0, 30px, 0);
    -ms-transform: translate3d(0, 30px, 0);
    transform: translate3d(0, 30px, 0);
    left: 0
}
/* Share Icons Css End */
/* AuthorDetails css start */
.authorDetails {
    position: relative;
    display: flex;
	align-items: center;
}
.authorDetails img {
    height: 41px;
    width: 41px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    float: left;
    margin-right: 10px
}
.authorDetails img, .authorDetails .author-avatar-block {
    height: 41px;
    width: 41px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    float: left;
    margin-right: 10px
}
.authorDetails .author-avatar-block {
    justify-content: center;
    align-items: center;
    color: #fff;
}
.authorDetails .postAuthorName {
    font: 700 13px/18px 'inter';
    white-space: nowrap;
    text-decoration: none;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis
}
.authorDetails .authrHandleTime {
    overflow: hidden;
    display: flex;
    align-items: center;
}
.authorDetails .authrHandle, .authorDetails .timePost {
    font: 400 10px/18px 'inter';
    text-decoration: none;
    display: inline-block;
    overflow: hidden;
    float: left;
    margin-right: 0;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.authorDetails .authrHandle {
    max-width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap
}
.flatCard.onlyTextCard h4 .authorDetails {
    position: absolute;
    bottom: 10px;
    width: 84%
}
.postAuthorDetails_{
    display: flex;
    align-items: initial;
    justify-content: start;
    flex-direction: column;
    width: 80%;
	flex-grow: 1;
}

@media only screen and (min-width:480px) and (max-width:610px) {
    .authorDetails .authrHandle, .authorDetails .timePost {
        display: block;
    }
}
