/* Social Action Start */
._socialAction {
    /* display: none; */
    background: rgba(0, 0, 0, .04);
    padding: 3px 8px;
    text-align: right
}
/* .flatCard.flickrTaggbox ._socialAction, .flatCard.pinterestTaggbox ._socialAction, .flatCard.rssTaggbox ._socialAction, .flatCard.tumblrTaggbox ._socialAction, .flatCard.vineTaggbox ._socialAction, .flatCard.yelpTaggbox ._socialAction {
    display: none!important
} */
._socialAction a {
    text-decoration: none;
    vertical-align: middle;
    font-size: 16px;
    padding: 5px 0;
    width: 26px;
    text-align: center;
    color: inherit;
    transition: all ease .3s
}
._socialAction a+.tooltip.top {
    margin-left: -10px!important
}
._socialAction a+.tooltip.top .tooltip-arrow {
    margin-left: 5px!important
}
._socialAction a.fa-share-square-o:hover:before {
    content: "\f14d"
}
._socialAction a.fa-retweet:hover {
    transform: rotateX(-180deg);
    -webkit-transform: rotateX(-180deg);
    -moz-transform: rotateX(-180deg)
}
._socialAction a.fa-heart-o:hover:before {
    content: "\f004"
}
._socialAction a.fa-thumbs-o-up:hover:before {
    content: "\f164"
}
._socialAction a.fa-comment-o:hover:before {
    content: "\f075"
}
/* Social Action End */
