  -webkit-line-clamp: 2!important;
  overflow: hidden;
  }
  .gallery_text_trim{
	display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3!important;
  overflow: hidden;
  }

  @media (min-width: 768px) and (max-width: 825px) {
  .gallery_text_trim {
