.tb_post_submit_container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    font-family: 'Inter', sans-serif;
}

.tb_post_submit_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 20px 15px;
    font-size: 14px;
    line-height: 2;
}
.tb_post_submit_btn .btn-scan{
    background-color: #000;
    border: 1px solid #000;
    font-weight: 500;
    color: #fff;
    transition: color .3s ease-in-out,background-color .3s ease-in-out,border-color .3s ease-in-out,box-shadow .3s ease-in-out;
    padding: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    min-height: 60px;
}
.tb_post_submit_btn .btn-scan .qr-image{
    /* width: 100px;
    height: 100px; */
    object-fit: cover;

    background: #fff !important;
    height: 70px;
    width: 70px;

    display: flex;
    align-items: center;
    justify-content: center;


}
.tb_post_submit_btn .btn-scan canvas{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.tb_post_submit_btn .btn-scan .qr-text{
    display: flex;
    padding: 15px 20px;
    font-size: 14px;
    align-items: center;
}
.tb_post_submit_btn .btn-scan .separator ~ .qr-text{
    padding: 15px 10px 15px 0px !important;
}
.tb_post_submit_btn .btn-scan .qr-text span{
    text-align: center;
    width: 130px;
}
.tb_post_submit_btn .separator ~ .qr-text span{
    width: 170px !important;
    word-break: break-word;
}
.tb_post_submit_btn .btn-scan .separator{
    width: 1px;
    background-color: #3e3e3e;
    margin: 0px 20px;
    height: 60px;
}

.tb_post_submit_btn .tb-sp-font {
    font-size: 32px;
    margin-right: 15px;
}
.tb_post_submit_btn .qr-text .tb-sp-font ~ span{
    width: auto;
}


@media (max-width: 575.98px) { 
    .tb_post_submit_btn .btn-scan{
        padding: 10px 15px;
    }
    .tb_post_submit_btn .btn-scan .separator{
        margin: 0px 10px 0px 20px;
    }    
}
.tb_modal{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 1.42857;
    color: #262626;
}

.tb_modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10000;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 0.3s ease-out 0s;
}
.tb_modal.in{
    opacity: 1;
}

a:hover, a:active, a:focus {
    text-decoration: none;
}

button:focus {
    outline: none;
    outline: -webkit-focus-ring-color auto 0px;
}
.tb_modal.show {
  display: block;
  opacity:1;
  visibility: visible;
  transition: opacity 300ms ease-in-out;
}

.tb_fade {
    transition: opacity 0.15s linear;
}

.tb_modal_dialog.show {
    -webkit-transform: none;
    transform: none;
}

.tb_modal_dialog {
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    min-height: calc(100% - 3.5rem);
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 20px 60px;
    max-width: 100%;
    z-index: 1;
    margin: 1.75rem auto;
    opacity: 0;
    transition-property: transform,-webkit-transform,opacity;
    transition-duration: .2s;
    transition-timing-function: ease-in;
    transition: all ease .3s;
    -moz-transition: all ease .3s;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
}

.tb_modal.in .tb_modal_dialog,
.tb_modal.show .tb_modal_dialog{
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
    transition: all 0.3s;
}
.tb_conetent{
    position: relative;
    display: flex;
    flex-direction: column;
    background-clip: padding-box;
    outline: 0;
    margin: 0 auto;
    max-width: calc(25vh + 25vw + 420px);
    width: 100%;
}

.tb_modal_body {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
}

