    width: 8px;
    background-color: rgb(0 0 0 / 20%);
    border-radius: 10px;
}

.tb_modal_body .mCS-light-thick.mCSB_scrollTools .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar, 
.tb_modal_body .mCS-light-thick.mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar {
    background-color: rgb(0 0 0 / 50%);
}

