    background: rgba(255, 255, 255, 0.6);
    position: absolute;
    top: 50%;
    width: 60px;
    height: 60px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    z-index: 1;
    cursor: pointer;
    opacity: 0.85;
