
.filterSection {
    display: block;
    width: 100%;
    text-align: center;
    padding: 22px 15px 15px
}

.poweredby_with_filters, .ff-filter-holder {
    vertical-align: middle;
    display: inline-block
}

.poweredby_with_filters .widget_filter_poweredBy {
    display: flex;
    align-items: center
}

.poweredby_with_filters, .ff-filter-holder .ff-filter {
    padding: 0 6px 10px
}

.filter_poweredBy_a {
    display: block;
    text-decoration: none
}

.ff-filter-holder .ff-filter {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    z-index: 2
}

.ff-filter-holder {
    position: relative
}

.ff-filter-holder .ff-filter button, .poweredby_with_filters a {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    font-size: 20px;
    border: 1px solid;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    border-color: #34495e;
    text-decoration: none;
    line-height: 38px;
    text-align: center;
    background-color: #fff;
    color: #34495e;
    transition: all ease .3s;
    -webkit-transition: all ease .3s;
    width: 42px;
    cursor: pointer;
}
/* .ff-filter-holder .ff-filter button:focus {
    outline: -webkit-focus-ring-color auto 1px;
} */
.monumentalst {
    outline: -webkit-focus-ring-color auto 0.5px;
}

.ff-filter-holder .ff-filter.ff-type-all button {
    width: auto;
    min-width: 42px;
    padding: 0 8px
}

.ff-filter-holder .ff-filter button:hover, .ff-filter-holder .ff-filter button.filterActive {
    background-color: #34495e;
    color: #fff
}

.filterSection .poweredby_with_filters a {
    width: auto;
    padding: 0 12px
}

.ff-filter-holder .ff-filter span {
    line-height: 38px;
    white-space: nowrap
}

a.filter_poweredBy_a span {
    font-size: 16px;
    padding-right: 5px
}

a.filter_poweredBy_a span, a.filter_poweredBy_a img {
    display: inline-block;
    vertical-align: middle
}

.ff-filter-holder .ff-filter button i {
    line-height: 38px;
    display: block;
    text-align: center;
    -ms-flex: 1 1 auto!important;
    flex: 1 1 auto!important
}

.filterSection .ff-filter-holder .ff-filter button.filter_poweredBy_a {
    width: auto
}

.filter1 .ff-filter-holder .ff-filter button {
    border-radius: 30px;
    padding: 0 3px
}

.filter1 .ff-filter-holder .ff-filter.ff-type-all button {
    padding: 0 30px;
    width: auto;
    border-radius: 30px
}

.filter1 .poweredby_with_filters, .filter1 .ff-filter-holder .ff-filter {
    padding: 0 12px 10px
}

.filter1 .poweredby_with_filters a {
    border-radius: 30px;
    padding: 0 20px
}

.filter1 .ff-filter-holder:before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 90%;
    height: 1px;
    border: 1px solid #9d9d9d;
    top: 19px;
    bottom: auto;
    z-index: 1;
    overflow: hidden;
    background-color: #fff;
    border-top: none!important
}

.filterSection.filter3 .poweredby_with_filters a {
    background-color: #fff!important;
    color: #4f2278!important;
    border-color: #4f2278!important
}

.filter4 .poweredby_with_filters, .filter4 .ff-filter-holder .ff-filter {
    padding: 0 10px 10px
}

.filter4 .poweredby_with_filters a {
    background-color: #fff!important;
    border: none!important;
    color: #613986!important
}

.filter4 .ff-filter-holder .ff-filter button, .filter4 .poweredby_with_filters a {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
    -webkit-box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
    -moz-box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
    position: relative;
    border: none;
    font-size: 16px;
    width: auto;
    padding: 0 12px;
    border: none!important
}

.filterSection.filter4 .ff-filter-holder .ff-filter button:hover, .filterSection.filter4 .ff-filter-holder .ff-filter button.filterActive {
    background-color: #34495e;
    color: #fff;
    border: none!important
}

.filter4 .ff-filter-holder .ff-filter button i {
    margin-right: 15px;
    width: 20px
}

.filter4 .ff-filter-holder .ff-filter button::before {
    content: '';
    border-left-width: 1px;
    border-left-style: solid;
    border-color: inherit;
    display: block;
    width: 1px;
    height: 60%;
    left: 38px;
    top: 20%;
    position: absolute;
    opacity: .5
}
.poweredBy_Remove a::before {
    content: none!important;
}


