@font-face {
    font-family: airbnb;
    src: url(./fonts/airbnb.eot?s7nv64);
    src: url(./fonts/airbnb.eot?s7nv64#iefix) format('embedded-opentype'), url(./fonts/airbnb.ttf?s7nv64) format('truetype'), url(./fonts/airbnb.woff?s7nv64) format('woff'), url(./fonts/airbnb.svg?s7nv64#airbnb) format('svg');

    font-weight: 400;
    font-style: normal;
    font-display: block
}

.fa-airbnb:before {
    content: "\e900"
}

.fa-airbnb {
    font-family: airbnb;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

@media screen and (max-width:992px) {
    .filter4 .poweredby_with_filters, .filter4 .ff-filter-holder .ff-filter {
