* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Inter';
}
.testimonials {
    position: relative;
}
.testimonials .inner-testimonials .owl-dots {
    display: none;
}
.testimonials .inner-testimonials .owl-nav {
    position: absolute;
    top: 45%;
    transform: translateY(-45%);
    width: 100%;
}
.testimonials .owl-carousel {
    overflow: hidden;
}
.testimonials .testimonial_detail p {
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.testimonials .owl-carousel .owl-nav button.owl-next, .testimonials .owl-carousel .owl-nav button.owl-prev {
    position: absolute;
    color: #fff;
    font-size: 40px;
    box-shadow: -2px 0 5px rgba(0,0,0,0.3);
    background: rgba(255, 255, 255, 0.6);
    position: absolute;
    top: 50%;
    width: 60px;
    height: 60px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    z-index: 1;
    cursor: pointer;
    opacity: 0.85;
    transition: all 0.2s;
    backface-visibility: hidden;
}
.testimonials .owl-carousel .owl-nav button.owl-next:hover, .testimonials .owl-carousel .owl-nav button.owl-prev:hover {
    background: rgba(255, 255, 255, 0.85);
}
.testimonials .owl-carousel .owl-nav button.owl-next:focus, .testimonials .owl-carousel .owl-nav button.owl-prev:focus {
    outline: 0;
}
.testimonials .owl-carousel .owl-nav button.owl-prev {
    left: 0;
    transform: translate3d(-50%, -50%, 0);
}
.testimonials .owl-carousel .owl-nav button.owl-next {
    right: 0;
    transform: translate(50%, -50%);
}
.testimonials .owl-carousel .owl-nav button.owl-prev span {
    position: absolute;
    left: 34px;
    top: 45%;
    transform: translateY(-50%);
    color: #2b2b2b;
}
.testimonials .owl-carousel .owl-nav button.owl-next span {
    position: absolute;
    right: 34px;
    top: 45%;
    transform: translateY(-50%);
    color: #2b2b2b;
}
.testimonials .test_img {
    position: relative;
    -webkit-transition: .4s ease;
    transition: .4s ease;
    opacity: 0.6;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 500px;
}
.testimonials #slide-testimonal .active.center .test_img {
    opacity: 1;
}
.testimonials .test_img:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.9);
    transition: 0.38s;
}
.testimonials #slide-testimonal .active.center:hover .test_img:after {
    opacity: 1;
}
.testimonials .inner-testimonials {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}
.testimonials .custome_slide.owl-carousel .owl-item.active.center {
    /* transform: scale(1.3); */
    margin-top: 0 !important;
    position: relative;
    z-index: 999;
    -webkit-transform: scale(1);
    transform: scale(1);
}
.testimonials .custome_slide.owl-carousel .owl-item {
    /* transform: scale(0.7); */
    /* padding: 10px 0px; */
    transition: 0.4s ease;
}
.testimonials .testimonial_detail {
    display: none;
    position: relative;
    text-align: center;
    color: #ffffff;
    z-index: 1;
}
.testimonials .testimonial_detail h4 {
    font-weight: 600;
}
.testimonials .testimonial_detail h4, .testimonials .testimonial_detail p, .testimonials .testimonial_detail blockquote {
    margin: 0;
}
.testimonials .cardCTA-post-theme {
    margin-bottom: 15px;
}
.testimonials .testimonial_detail p, .testimonials .testimonial_detail blockquote {
    /* font-size: 12px; */
}
.testimonials .testimonial_detail blockquote {
    max-width: 400px;
    margin: 0 auto 15px;
}
.testimonials .testimonial_detail .author-details, .testimonials .testimonial_detail .testimonial_detail--footer {
    padding: 20px 20px 0;
}
.testimonials .testimonial_detail .author-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
}
.testimonials .testimonial_detail .author-details .timePost {
    position: relative;
    padding-left: 12px;
    display: inline-block;
}
.testimonials .testimonial_detail .author-details .timePost::before {
    content: '';
    position: absolute;
    width: 3px;
    height: 3px;
    background-color: #ffffff;
    border-radius: 50%;
    left: 3px;
    top: 8px;
}
.testimonials .testimonial_detail .author-details > div {
    display: flex;
    align-items: center;
}
.testimonials .testimonial_detail .author-details > div img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
}
.testimonials .testimonial_detail .author-details svg {
    font-size: 30px;
}
.testimonials .custome_slide.owl-carousel .active .testimonial_detail {
    display: none;
    transition: 0.6s;
}
.testimonials .custome_slide.owl-carousel .active.center:hover .testimonial_detail { 
    display: flex !important;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}
.testimonials .shareWrapperPostTheme {
    position: relative;
}
.testimonials .shareWrapperPostTheme a {
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
}
.testimonials .shareWrapperPostTheme a svg {
    margin-right: 8px;
}
.testimonials .shareWrapperPostTheme .popover-content {
    display: none;
    position: absolute;
    bottom: 100%;
    padding-bottom: 10px;
    width: 170px;
    z-index: 6;
}
.testimonials .popover-content-inner {
    position: relative;
    background: #2f353a;
    border-radius: 4px;
    font-family: 'inter';
    box-shadow: 0 4px 16px rgba(0,0,0,0.2);
}
.testimonials .popover-content-inner:before {
    content: '';
    background: #2f353a;
    position: absolute;
    bottom: -5px;
    left: 20px;
    width: 10px;
    height: 10px;
    transform: rotateZ(-45deg);
}
.testimonials .popover-content-inner a {
    display: flex;
    align-items: center;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    line-height: 1;
    border-top: 1px solid #42474c;
    padding: 12px;
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;
    text-align: left;
}
.testimonials .popover-content-inner a:hover {
    background: #42474c;
}
.testimonials .cardCTA-post-theme a {
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    color: #000000;
    text-decoration: none;
    padding: 8px 20px;
    display: inline-block;
    border-radius: 4px;
    font-size: 14px;
    background-color: #ffffff;
    position: relative;
    /* display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden; */
    white-space: nowrap;
    overflow: hidden;
  text-overflow: ellipsis;
   max-width: 400px;
}
.testimonials .share-icons-post-theme {
    display: flex;
    justify-content: space-between;
    margin: 0 -20px;
    background: rgba(0, 0, 0, 0.2);
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    padding: 10px 20px;
    font-size: 18px;
}
.testimonials .social-action a {
    margin-right: 10px;
}
.testimonials .social-action a:last-child {
    margin-right: 0;
}
.testimonials .social-action a svg path {
    fill: #ffffff;
}
.testimonials .owl-carousel{
    display: block!important;
}
.testimonials .tb_youtube_post_ico {
    z-index: 0;
}

@media only screen and (max-width:767px){
    .testimonials .test_img {
        margin: 0;
    }
    .testimonials .testimonial_detail {
        left: inherit;
        width: 100%;
    }
    .testimonials .testimonial_detail blockquote {
        max-width: 500px;
        margin: 0 auto 20px;
    }
}
.testimonials .owl-nav button{
    border: none;
}

.testimonials h4 {
    font-size: 16px!important;
}
.testimonials .author-font-size-10px {
    font-size: 10px!important;
}
.testimonials .author-font-size-12px {
    font-size: 12px!important;
}
.testimonials .author-font-size-14px {
    font-size: 14px!important;
}
.testimonials .cardCTA-post-theme a:hover{
    color:#000000;
}

.dotLoader {
    width: 80px;
    height: 80px;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 25%;
    flex: 0 0 25%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.dot-loader {
    height: 18px;
    width: 18px;
    border-radius: 50%;
    background-color: #323232;
    position: relative;
    -webkit-animation: 1.2s grow ease-in-out infinite;
    animation: 1.2s grow ease-in-out infinite;
}

.dot-loader--2 {
    -webkit-animation: 1.2s grow ease-in-out infinite 0.15555s;
    animation: 1.2s grow ease-in-out infinite 0.15555s;
    margin: 0 5px;
}
.dot-loader--3 {
  -webkit-animation: 1.2s grow ease-in-out infinite 0.3s;
  animation: 1.2s grow ease-in-out infinite 0.3s;
}

@-webkit-keyframes grow {
  0%, 40%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes grow {
  0%, 40%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}