body.ieBrowser .blur-bg{
	filter: none !important; 
	-ms-filter: none !important; 
	-webkit-filter: none !important; 
	opacity:0.15 !important;
}
.themeGallerySquarePhoto {
    padding-top: 45px;
}
.themeGallerySquarePhoto .flatCard {height:300px; }
.themeGallerySquarePhoto .flatCard .post, .themeGallerySquarePhoto .flatCard .post .image, .themeGallerySquarePhoto .flatCard .postContent{height:100%;border: 0px;}
.themeGallerySquarePhoto .flatCard .img-responsive{
	visibility: hidden !Important; 
	position: absolute;
	top: 50%;
	left:50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}
.themeGallerySquarePhoto .flatCard .image .blur-bg{
    position: static;
    width: inherit;
    height: inherit;
    filter: blur(0px);
    -webkit-filter: blur(0px); 
    -ms-filter: blur(0px);
	-moz-filter: blur(0px);	
    background-size: cover !important;
    background-position: center center !important;
    opacity: 1;
    object-fit: cover;
    width: 100%;
    object-position: center;
}
body.ieBrowser .themeGallerySquarePhoto .flatCard .image .blur-bg{opacity:1 !Important;}
.themeGallerySquarePhoto .flatCard .postContentCard{
    border: 0px;
    position: absolute;
    left: 0px;
    top: 0px;
    width: -webkit-fill-available;
    width: stretch;
    width: -moz-available;
    height: -webkit-fill-available;
    height: stretch;
    height: -moz-available;
    opacity: 0;
    margin: 7px 7px 7px 7px;
    background-color: rgba(0,0,0,0.7);
    transition: all 0.8s ease-in-out;
    -webkit-transition: all 0.8s ease-in-out;
    -moz-transition: all 0.8s ease-in-out;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    -ms-flex-direction: column;
    flex-direction: column;
    height: calc(100% - 14px);
    width: calc(100% - 14px);
    z-index: 100;
}
.themeGallerySquarePhoto .flatCard .post:hover .postContentCard{opacity: 1;}
.themeGallerySquarePhoto .flatCard .post:hover .postContent .postContentCard{opacity: 1;}
/* .themeGallerySquarePhoto .flatCard .post:hover h4{ color:#fff; } 
   .themeGallerySquarePhoto .flatCard .post:hover .sepratedot{ color:#fff; } */
.themeGallerySquarePhoto .flatCard .postContentCard .postedTxt {
    color: #fff;
    text-align: center;
    padding: 5px 18px 0 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}
.themeGallerySquarePhoto .flatCard .postNetwork{position: absolute;top: 18px; right:18px; z-index:100;opacity:0.4;transition: all 0.8s ease-in-out;-webkit-transition: all 0.8s ease-in-out;-moz-transition: all 0.8s ease-in-out;}
.themeGallerySquarePhoto .flatCard .postNetwork i{ font-size: 24px; line-height: 18px; color:#fff;}
.themeGallerySquarePhoto .flatCard h4 .authorDetails{/* position: absolute;top: 0px; */width: 100%;display: flex;flex-direction: column;justify-content: center;align-items: center; }
.themeGallerySquarePhoto .authorDetails img {
    height: 41px;
    width: 41px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    margin-right: 0;
    border: 1px solid #ffffff;
	margin-bottom: 7px;
}
.themeGallerySquarePhoto .authorDetails .postAuthorName {
    font: 700 14px/20px 'inter';
}
.themeGallerySquarePhoto .flatCard .postedTxt p, .flatCard .postedTxt h1 {
    line-height: 112%;
	padding-top: 2px;
}
.themeGallerySquarePhoto .postContent:hover .shareWrapper {
    top: 8px;
}
.themeGallerySquarePhoto .post .postContentCard .postContentCard_innerDiv{/* position: absolute;top: 28%; */width: 100%;flex-direction: column; justify-content: center; align-items: center; display: flex; height: 100%;}
.themeGallerySquarePhoto .cardCTA {display: flex;flex-direction: column;width: 94%;justify-content: center;align-items: center;position: absolute;padding-top: 56%;}
.themeGallerySquarePhoto .image_overlay_black{
	position: absolute;
    top: 0;
    width: 100%;
    height: 40%;
    background-color: transparent;
    background-image: linear-gradient(to bottom, #000000, #00000000);
	transition: all 0.8s ease-in-out;
    -webkit-transition: all 0.8s ease-in-out;
    -moz-transition: all 0.8s ease-in-out; 
    display: none;
}
.themeGallerySquarePhoto .cardCTA a {
	max-width: 98%;
	width: auto;
    min-width: 124px;
    padding: 8px 10px 8px 10px;
	border-radius: 34px;
}
.themeGallerySquarePhoto .shareWrapper a {
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.75);
}

.themeGallerySquarePhoto .flatCard .post:hover .image_overlay_black {
    opacity: 0;
}

.themeGallerySquarePhoto .flatCard:hover .postNetwork {
    opacity: 1;
}
