/**Classic Card Updated**/
.themeClassicCard {
    /* padding: 15px;  */
    position: relative;}
.themeClassicCard .flatCard .post{
    -webkit-box-shadow: 1px 1px 5px #00000021;
    -moz-box-shadow: 1px 1px 5px #00000021;
    box-shadow: 1px 1px 5px #00000021;
    background-clip: padding-box;
	border:0px;
	border-radius: 7px;
}
.themeClassicCard .authrHandleTime {
    float: left;
    display: flex;
    align-items: center;
    width: 90%;
}
.themeClassicCard .flatCard .cardCTA {margin-top: 15px; margin-bottom: 15px; padding: 0 10px;}
.themeClassicCard .flatCard .cardCTA a {padding: 10px 0 10px 8px;}
.themeClassicCard .flatCard ._socialAction {background:#fff; padding: 10px 0; margin: 0 10px; border-top: 2px solid #E9E9E9; text-align: left; font-size: 0; justify-content: space-between; flex-wrap: wrap;}
.themeClassicCard .flatCard ._socialAction .social-shareWrapper {
    position: relative;
}
.themeClassicCard .flatCard ._socialAction>div:only-child {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}
.themeClassicCard .flatCard ._socialAction .popover-content {
    display: none;
    position: absolute;
    bottom: 100%;
    padding: 0;
    padding-bottom: 10px;
    width: 170px;
    right: -1px;
    z-index: 6;
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner {
    position: relative;
    background: #2f353a;
    border-radius: 4px;
    box-shadow: 0 4px 16px rgba(0,0,0,0.2);
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner:before {
    content: '';
    background: #2f353a;
    position: absolute;
    bottom: -5px;
    right: 20px;
    width: 10px;
    height: 10px;
    transform: rotateZ(-45deg);
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner a {
    display: flex;
    align-items: center;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    line-height: 1;
    border-top: 1px solid #42474c;
    padding: 12px;
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;
    text-align: left;
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner a:first-child {
    border-top: 0;
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner a:hover {
    background: #42474c;
}
.themeClassicCard .flatCard ._socialAction .popover-content-inner a i {
    margin-right: 7px;
}
.themeClassicCard .flatCard ._socialAction a {width: auto; color: #6C727A; padding: 0 5px; font-family: 'Open Sans'; font-size: 12px; font-weight: 600; display: flex; align-items: center;}
.themeClassicCard .flatCard ._socialAction a svg {margin-right: 5px; width: 15px; height: auto;}
.themeClassicCard .flatCard.onlyTextCard .postedTxt { 
    position:static; 
    transform: none;
    -moz-transform: none;
    -webkit-transform: none;
    margin-top:0px;
    clear:both;
} 
.themeClassicCard .flatCard.onlyTextCard .postedTxt { padding: 0; margin: 10px 0; text-align:left;} 
.themeClassicCard .flatCard.onlyTextCard .postedTxt.decorateTxt { margin: 60px 0; text-align:center;} 
.flatCard.onlyTextCard .postContent p{padding: 0;}
.themeClassicCard .flatCard h4{ margin:0px;}
.themeClassicCard .flatCard.onlyTextCard h4 .authorDetails{position:static;} 
.themeClassicCard .flatCard.onlyTextCard h4 .postNetwork{    
    transform: none;
    -moz-transform: none;
    -webkit-transform: none;
    left:auto;
    top:auto;
    right:15px;
}
/* card preview new design */
@media only screen and (max-width:1024px) {
    .themeClassicCard {padding: 0;}
}
@media only screen and (max-width:580px) {
    .themeClassicCard .flatCard ._socialAction {justify-content: space-around;}
}
.themeStart5 .flatCard.onlyTextCard .postedTxt {
    position: static;
    transform: none;
    -moz-transform: none;
    -webkit-transform: none;
    margin: 0;
    /* padding: 50px 0 */
    padding: 0;
}
.themeStart5 .flatCard.onlyTextCard .postedTxt.decorateTxt {
    padding: 50px 0;
    text-align: center
}
.theme5_poweredBy {
    right: 5px;
    top: -40px
}
.theme5_poweredByMain {
    padding-top: 45px;
}
.theme5_poweredByMain {
    display: block!important
}
.themeClassicCard .flatCard h4 .postNetwork svg {
    width: 13.5pt!important;
    height: 13.5pt!important
}