.noPostSec {
    height: 100%;
    background: url(https://cloud.taggbox.com/widget/widget-embed/taggbox.png) no-repeat center top;
    background-size: cover;
    position: relative;
    min-height: 100vh;
    padding: 15px;
}

.noPostSec:after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background: #fff;
    z-index: 1;
    opacity: .55
}

.noPostContent {
    position: relative;
    height: calc(100vh - 30px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.noPostMsgBlock {
    height: auto;
    max-width: 700px;
    padding: 50px 40px;
    border-radius: 6px;
    background: #fff;
    z-index: 5;
    text-align: center;
    position: relative;
    z-index: 9;
    box-shadow: 0px 3px 8px 0px rgba(50, 50, 50, 0.2);
    -moz-box-shadow: 0px 3px 8px 0px rgba(50, 50, 50, 0.2);
    -moz-box-shadow: 0px 3px 8px 0px rgba(50, 50, 50, 0.2);
}

.submit_pic_btn__00 {
    position: relative;
    z-index: 9;
}

.noPostMsgBlock p strong {
    color: #ff4051;
    font-weight: 600
}

.noPostMsgBlock .product-logo {
    position: absolute;
    right: 0;
    bottom: -35px;
    width: 170px;
    text-decoration: none;
}

.noPostMsgBlock p {
    font-size: 16px;
    color: #959595;
    font-weight: 400;
    max-width: 600px;
    margin: 0 auto 30px;
}

.noPostMsgBlock a:hover {
    text-decoration: none;
}

.noPostMsgBlock img {
    max-width: 90px;
}

@import url(https://fonts.googleapis.com/css?family=Comfortaa:300,400,500,600,700&display=swap);
.noPostMsgBlock .tb-pro-name {
    display: inline-block;
    line-height: 30px;
    padding-left: 5px;
    font-size: 14px;
    color: #2b2b2b;
    vertical-align: middle;
    transition: ease all .2s;
    font-family: 'comfortaa', sans-serif;
}

.noPostMsgBlock h2 {
    font-size: 25px;
    font-weight: 600;
    margin: 0 0 15px 0;
    color: #2b2b2b;
    text-transform: capitalize;
}

.noPostMsgBlock a.btn-danger {
    background-color: #ff4051;
    border-color: #ff4051;
    font-size: 16px;
    min-width: 120px;
    padding: 8px;
    display: inline-block;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    text-decoration: none;
    -ms-user-select: none;
    user-select: none;
    padding: 8px 10px;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .42rem;
    transition: color .3s ease-in-out,background-color .3s ease-in-out,border-color .3s ease-in-out,box-shadow .3s ease-in-out;
}

.noPostMsgBlock a.btn-danger:hover{
    color: #fff;
    background-color: #e23442;
    border-color: #e23442;
}

.product-logo {
    position: absolute;
    right: 0;
    bottom: -35px;
    width: 170px;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: end;
  }
  
  .product-logo img {
    max-width: 90px;
  }
  