
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');
@import url('../../../modalPopUp/css/inter-fonts.css');
.tb_post_submit_container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    font-family: 'Inter', sans-serif;
}

.tb_post_submit_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    padding: 20px 15px;
    font-size: 14px;
    line-height: 2;
}
.tb_post_submit_btn .btn-scan{
    background-color: #000;
    border: 1px solid #000;
    font-weight: 500;
    color: #fff;
    transition: color .3s ease-in-out,background-color .3s ease-in-out,border-color .3s ease-in-out,box-shadow .3s ease-in-out;
    padding: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    min-height: 60px;
}
.tb_post_submit_btn .btn-scan .qr-image{
    /* width: 100px;
    height: 100px; */
    object-fit: cover;

    background: #fff !important;
    height: 70px;
    width: 70px;

    display: flex;
    align-items: center;
    justify-content: center;


}
.tb_post_submit_btn .btn-scan canvas{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.tb_post_submit_btn .btn-scan .qr-text{
    display: flex;
    padding: 15px 20px;
    font-size: 14px;
    align-items: center;
}
.tb_post_submit_btn .btn-scan .separator ~ .qr-text{
    padding: 15px 10px 15px 0px !important;
}
.tb_post_submit_btn .btn-scan .qr-text span{
    text-align: center;
    width: 130px;
}
.tb_post_submit_btn .separator ~ .qr-text span{
    width: 170px !important;
    word-break: break-word;
}
.tb_post_submit_btn .btn-scan .separator{
    width: 1px;
    background-color: #3e3e3e;
    margin: 0px 20px;
    height: 60px;
}

.tb_post_submit_btn .tb-sp-font {
    font-size: 32px;
    margin-right: 15px;
}
.tb_post_submit_btn .qr-text .tb-sp-font ~ span{
    width: auto;
}


@media (max-width: 575.98px) { 
    .tb_post_submit_btn .btn-scan{
        padding: 10px 15px;
    }
    .tb_post_submit_btn .btn-scan .separator{
        margin: 0px 10px 0px 20px;
    }    
}