@font-face {
    font-family: giphy;
    src: url(./fonts/giphy.eot);
    src: url(./fonts/giphy.eot) format('embedded-opentype'), url(./fonts/giphy.ttf) format('truetype'), url(./fonts/giphy.woff) format('woff'), url(./fonts/giphy.svg) format('svg');
    font-weight: 400;
    font-style: normal;
    font-display: block
}
.fa-youtube:before {
    content: "\f16a"
}
.fa-giphy:before {
    content: "\e912"
}
.fa-slack{
    font-family: 'TaggboxSocialFonts';
    speak-as: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.fa-giphy {
    font-family: giphy!important;
    speak: none!important;
    font-style: normal!important;
    font-weight: 400!important;
    font-variant: normal!important;
    text-transform: none!important;
    -webkit-font-smoothing: antialiased!important;
    -moz-osx-font-smoothing: grayscale!important;
}

@font-face {
  font-family: taggbox-workplace-icon;
  src: url('https://cloud.taggbox.com/widget/widget-embed/taggbox-icon.eot?f135rl');
  src: url('https://cloud.taggbox.com/widget/widget-embed/taggbox-icon.eot?f135rl#iefix') format("embedded-opentype"), url('https://cloud.taggbox.com/widget/widget-embed/taggbox-icon.ttf?f135rl') format("truetype"), url('https://cloud.taggbox.com/widget/widget-embed/taggbox-icon.woff?f135rl') format("woff"),
      url('https://cloud.taggbox.com/widget/widget-embed/taggbox-icon.svg?f135rl#taggbox-icon') format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: block;
}

.fa-workplace {
  font-family: taggbox-workplace-icon !important;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fa-workplace:before {
  content: "\e900";
}