.mt-radio.squareRadio {
    transition: all ease .3s;
    font-weight: 500;
    line-height: 20px;
    font-size: 13.5px;
    color: #545454;
    padding-top: 8px;
    padding-bottom: 8px;
    position: relative;
    margin-bottom: 10px
}

.mt-radio.squareRadio:after {
    content: '';
    border-bottom: 1px solid #f1f1f1;
    left: 0;
    right: 30px;
    bottom: 0;
    height: 1px;
    display: block;
    position: absolute
}

.mt-radio.squareRadio:hover:after {
    border-bottom-color: #dbdbdb
}

.mt-radio.squareRadio span, .mt-radio.squareRadio>input:checked~span {
    top: 15px;
    border-radius: 0!important;
    border-width: 2px!important;
    height: 20px;
    width: 20px
}

.mt-radio.squareRadio span:after {
    left: 6px;
    top: 2px;
    width: 5px;
    height: 10px;
    border: solid #888;
    border-width: 0 2px 2px 0;
