.loaderBlock.clickable:not(.filterSection) {
    text-align: center;
    box-shadow: 0 2px 2px rgba(0, 0, 0, .12);
    border-radius: 5px;
    margin: 20px 0 0;
    padding: 40px 10px 50px;
    position: relative;
    overflow: hidden;
    transition: all ease .3s
}

.clickable .title {
    font-size: 16px;
    font-weight: 600;
    color: #323232;
    margin-top: 20px
}

.clickable p {
    font-size: 12px;
    color: #6e7073;
    margin: 10px 0 0
}

.clickable:not(.filterSection):hover {
    cursor: pointer
}

.clickable:hover .svgIco, .clickable:hover .title, .clickable:hover p {
    filter: blur(2px)
}

.loaderBlock.clickable:not(.filterSection)::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: 0 0;
    transition: all ease .4s;
    z-index: 9
}

.loaderBlock.clickable:hover::before {
    background: rgba(0, 0, 0, .7)
}

.loaderBlock.clickable:hover .btn.btnGreenOk {
    opacity: 0
}

.loaderAction {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    width: 100%;
    z-index: 9
}

.clickable .loaderAction .btn {
    visibility: hidden;
    opacity: 0;
    transition: all ease .3s;
    border: 2px solid #fff
}

.clickable:hover .loaderAction .btn {
    visibility: visible;
    opacity: 1
}

.loaderAction .btn {
    width: 38px;
    height: 38px;
    background-color: #fff;
    border: 0;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 50%
}

.loaderAction .btn.btnSelectLoader {
    background-image: url(https://cloud.taggbox.com/widget/widget-embed/check-icon.svg);
    position: static
}

.loaderAction .btn.btnEditLoader {
    background-image: url(https://cloud.taggbox.com/widget/widget-embed/edit-icon.svg)
}

.loaderBlock.clickable .btn.btnGreenOk {
    background-image: url(https://cloud.taggbox.com/widget/widget-embed/check-green-icon.svg);
    width: 28px;
    height: 28px;
    position: absolute;
    left: auto;
    right: 15px;
    top: 15px;
    border: 0;
    background-repeat: no-repeat;
    background-position: center center;
    display: none
}

.loaderBlock.clickable.active .btn.btnGreenOk {
    display: block;
    border: 0
}

.loaderBlock.clickable:hover .btn.btnGreenOk:hover {
    border: 0
}

.loaderBlock.clickable:hover .btn:hover {
    border: 2px solid #000
}

