.taggModal .slideBtn {
    position: absolute;
    z-index: 9999;
    color: #fff;
    padding: 10px;
    height: 50px;
    width: 50px;
    top: 50%;
    margin-top: -25px
}

.taggModal .slideBtn::before {
    content: '';
    border: 1px solid #a6a6a6;
    display: block;
    height: 100%;
    transition: all ease .3s
}

.taggModal .slideBtn.next-btn {
    right: -65px;
    padding: 10px 20px 10px 0
}

.taggModal .slideBtn.next-btn::before {
    border-left: 0;
    border-bottom: 0;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg)
}

.taggModal .slideBtn.pre-btn {
    left: -65px;
    padding: 10px 0 10px 20px
}

.taggModal .slideBtn.pre-btn::before {
    border-right: 0;
    border-bottom: 0;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg)
}

.taggModal .slideBtn:hover::before {
    border-color: #fff
}

