.horizontalColumns {position: relative}
.horizontalColumns .ladda-spinner, .horizontalColumns .ladda-progress{ display:none !important;}
.horizontalColumns .slick-slide, .horizontalColumns .slick-slide:focus, .horizontalColumns .slick-slide:active{ outline:none;}
.horizontalColumns .slick-slide div:focus{ outline:none;}
.horizontalColumns .flatCard .post{ background-clip: padding-box; border:0px; border-radius:10px; overflow:hidden; box-shadow:0px 1px 3px rgba(0, 0, 0, 0.22);-webkit-box-shadow:0px 1px 3px rgba(0, 0, 0, 0.22); -moz-box-shadow:0px 1px 3px rgba(0, 0, 0, 0.22); min-height:230px; margin:10px 0 5px;}
.horizontalColumns .flatCard *{text-align:center;}
.horizontalColumns .authorDetails img{ position:static; float: none; clear:both; margin:-50px 0 0 0; width:50px; height:50px; border:1px solid #000; }
.horizontalColumns .slick-slide-numberOfCoumn .videoIcon img{
width: 10px;
}

.horizontalColumns .slider {
    padding: 5px 50px;
}

.horizontalColumns .slick-next:before{
    background-image: url('data:image/png;base64,');
}
.horizontalColumns .slick-prev:before{
    background-image: url('data:image/png;base64,');
}

.horizontalColumns .slick-prev:before, .horizontalColumns .slick-next:before{
    content: '';
    height: 30px;
    width: 30px;
    background-position: center;
    display: block;
    background-size: contain;
}

.horizontalColumns .slick-prev {
    left: -45px;
}

.horizontalColumns .slick-next {
    right: -45px;
}

.horizontalColumns .slick-prev, .horizontalColumns .slick-next {
    top: 30%;
    width: 28px;
    height: 28px;
}
.horizontalColumns .slick-slide-numberOfCoumn1 .videoIcon img{
width: 20px;
}
.horizontalColumns .authorDetails .authrHandle, .horizontalColumns .authorDetails .timePost, .horizontalColumns .sepratedot{ float:none;}
.horizontalColumns .authorDetails .authrHandleTime{ line-height:100%;}
.horizontalColumns .authorDetails .authrHandleTime *{vertical-align: middle; display:inline-block; line-height: inherit; float:none; clear:both;}
.horizontalColumns .flatCard h4 .postNetwork {width: auto; height: auto; float:none; margin:15px auto 0px auto;}
.horizontalColumns .flatCard h4 .postNetwork i{ font-size:30px !important;}
.horizontalColumns .flatCard .cardCTA, .horizontalColumns .flatCard.onlyTextCard .cardCTA{ margin-top: 10px; }
.horizontalColumns .flatCard.onlyTextCard h4 .postNetwork, .horizontalColumns .flatCard.onlyTextCard .postContent .postedTxt{position:static; transform:none; -moz-transform:none; -webkit-transform:none;}
.horizontalColumns .flatCard.onlyTextCard .authorDetails img{margin-top:10px;}
.horizontalColumns .flatCard.onlyTextCard .authorDetails .postAuthorName{margin-top:10px;}  
.horizontalColumns .flatCard .post p{line-height:24px;}
.horizontalColumns .flatCard .authorDetails .postAuthorName{ font-size:14px; max-width:80%; margin:0 auto;}
.horizontalColumns .flatCard .authorDetails .authrHandle, .horizontalColumns .flatCard .authorDetails .timePost { font-size:12px;}
.horizontalColumns .flatCard .postContentCard{ padding:10px 15px;}
.horizontalColumns .authorDetails img{ margin:0 0 15px 0; width:45px; height:45px; border: 2px solid #fff; background: #fff; box-shadow: 0px 1px 2px rgba(120, 120, 120, 0.30); -webkit-box-shadow: 0px 1px 2px rgba(120, 120, 120, 0.30); -moz-box-shadow: 0px 1px 2px rgba(120, 120, 120, 0.30);}
.horizontalColumns .flatCard.onlyTextCard .post{/* display: flex; align-items: center; justify-content: center; */text-align: center;padding: 150px 0 0px 0;min-height: auto;}
.horizontalColumns .flatCard.onlyTextCard .postedTxt{margin-bottom:40px;}
.horizontalColumns .flatCard.onlyTextCard h4 .postNetwork{margin: 0px auto 0px auto;}
.horizontalColumns .flatCard.onlyTextCard .authorDetails, .horizontalColumns .flatCard.onlyVideoNImg .authorDetails { margin-top: 0px; }
.horizontalColumns .flatCard.onlyTextCard h4 .authorDetails{position:absolute;bottom:auto;top: 50px;width: 100%;left: 0;}
.horizontalColumns .flatCard.onlyTextCard .authorDetails img { margin: 0px;} 
.horizontalColumns .flatCard.onlyVideoNImg .postContentCard{ position: absolute; width: 100%; padding: 20px 0; top: 220px; bottom: 0;}
.horizontalColumns .flatCard.onlyVideoNImg .postContentCard h4{ position: absolute;  width: 100%; top: 50%; transform: translateY(-50%); -webkit-transform: translateY(-50%); -moz-transform: translateY(-50%);}
.horizontalColumns .flatCard.onlyVideoNImg .authorDetails img{ margin-top:0;} 

.slick-slide img{
	display: inline;
}
.horizontalColumns.slickCloneHide .slick-cloned{ display: none; }

.horizontalColumns .theme47_poweredBy {
    right: 5px;
    top: -40px
}

.theme47_poweredBy .poweredbywrapper {
    background-color: rgba(255, 255, 255, .8)!important;
    border: none!important;
    padding: 9px 20px!important
}
.theme47_poweredBy {
    right: 50px;
    top: -40px
}
.theme47_poweredByMain {
    padding-top: 40px;
    display: block!important
}

.horizontalColumns .authorDetails {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
}
@media only screen and (max-width:768px){
	.horizontalColumns .slider {
        padding: 5px 40px;
    }
    .horizontalColumns .slick-prev:before, .horizontalColumns .slick-next:before {
        height: 24px;
        width: 24px;
    }
    .horizontalColumns .slick-next {
        right: -32px;
    }
    .horizontalColumns .slick-prev {
        left: -32px;
    }
    .horizontalColumns .slick-prev, .horizontalColumns .slick-next {
        top: 30%;
        width: 24px;
        height: 24px;
    }
}

.horizontalColumns .flatCard.onlyTextCard .post:focus{
	border: none;
}