.text_trim{
	display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5!important;
  overflow: hidden;
  }
  .horizontal_text_trim{
	display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2!important;
  overflow: hidden;
  }
  .gallery_text_trim{
	display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3!important;
  overflow: hidden;
  }

  @media (min-width: 768px) and (max-width: 825px) {
  .gallery_text_trim {
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1!important;
		white-space: normal;
	  }
}
@media (min-width: 825px) {
  .gallery_text_trim {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2!important;
	white-space: normal;
	}
}
   @supports (display: -webkit-box) {
	.text_trim {
	  display: -webkit-box;
	  -webkit-box-orient: vertical;
	  -webkit-line-clamp: 5!important;
	  white-space: normal;
	}
	.horizontal_text_trim {
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2!important;
		white-space: normal;
	}
	
  }

  .taggbox-mobile-banner {
	background-color: rgb(19, 115, 123);
	min-height: 100px;
	width: 100%;
	padding: 20px;
	text-align: center;
	color: #ffffff;
	word-break: break-word;
}
.taggbox-mobile-banner img {
	max-width: 100px;
	width: 100%;
}
.taggbox-mobile-banner p {
	font-size: 20px;
	margin-bottom: 0;
}
.taggbox-mobile-banner h1 {
	margin-bottom: 0;
	font-size: 40px;
	margin: 0;
}
@media only screen and (max-width:767px){
	.taggbox-mobile-banner img {
		max-width: 100px;
	}
	.taggbox-mobile-banner p {
		font-size: 16px;
	}
	.taggbox-mobile-banner h1 {
		font-size: 25px;
	}
}
