
.tb_st_card_container{
    font-family: 'Inter';
    max-width: 500px;
    margin: 0 auto;
    overflow: hidden;
    transition: all .2s ease-in-out;
    cursor: pointer;
}

.tb_st_card_wrapper {
    width: 100%;
    margin: 0 auto;
    position: relative;
    padding-bottom: 200%;
    height: auto;
    border-radius: calc(1.5vw + 5px);
    overflow: hidden;
    background-color: #fff;
}

.slick-center .tb_st_card_wrapper_00{
    opacity: 1;
}

.tb_st_author_img {
    display: flex;
    align-items: self-start;
    justify-content: center;
    flex-grow: 1;
    width: 30%;
    max-width: 100px;
    position: relative;
    z-index: 2;
}
.tb_st_author_img_00 {
    width: 100%;
    display: flex;
    margin: 0 auto;
    padding-bottom: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
    flex-shrink: 0;
    background-color: #fff;
    box-shadow: 0 0 0px 2px #fff;
}

.tb_st_play_btn {
    position: absolute;
    top: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 18%;
    max-width: 46px;
    left: 0;
    right: 0;
    margin: 0 auto;
}

.tb_st_card_wrapper_00 {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    width: 100%;
    opacity: 0.5;
}

.tb_st_card_content_0{
    height: 100%;
    width: 100%;
}

.tb_st_media_img {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #fff;
}

.tb_st_media_img video {
    width: 100%;
    height: 100%;
    background-color: #000;
}

.tb_st_media_img iframe{ height: 100%;}
.tb_st_img00 {
    max-width: 100%;
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_st_content_wrap{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    padding: 8%;
}
.tb_st_card_wrapper_00:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 20%;
    max-height: 120px;
    background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 75%, rgba(0,0,0,1) 100%); 
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.8) 75%,rgba(0,0,0,1) 100%);
    background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.8) 75%,rgba(0,0,0,1) 100%); 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 ); 

}
.tb_st_author_info {
    margin-top: auto;
    font-size: calc(15px + 0.4vw);
    width: 100%;
    position: relative;
    z-index: 2;
    text-align: center;
}
.tb_st_author_001 {
    margin-bottom: 5px;
    width: 100%;
}

.tb_st_social_icon {
    font-size: 24px;
    color: #fff;
}

.tb_st_author_name {
    text-align: center;
    font-size: 50%;
}

.tb_st_seprate_dot {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin: 0 6px;
    flex-shrink: 0;
    background-color: #fff;
}

.tb_st_post_time {
    font-size: 60%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 35%;
    width: initial;
}

.tb_st_author_info {
    color: #fff;
}
.tb_st_hashtag {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    margin: 5px 0;
}
.tb_st_multi_slide .slick-active { 
    padding: 50px 0; 
}
.tb_st_multi_slide .slick-center .tb_st_card_container{
    transform: scale(1.15);
    z-index: 21;
    position: relative;
}

.tb_st_slider_prev_arrow, .tb_st_slider_next_arrow {
    margin-top: 0px;
    width: 38px;
    height: 38px;
    z-index: 2;
    margin: 0 auto;
    left: 0;
    top: calc(50% - 25px);
    right: 0;
    position: absolute;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.tb_st_slider_prev_arrow svg, .tb_st_slider_next_arrow svg{
    width: 100%;
    height: 100%;
    display: inline-block;
    opacity: .8;
}

.tb_st_slider_prev_arrow:hover svg, .tb_st_slider_next_arrow:hover svg{
    opacity: .95;
}

.tb_st_slider_prev_arrow {
    left: -10%;
    right: 0;
}

.tb_st_slider_next_arrow {
    left:0;
    right: -10%;
}
.tb_st_slider_prev_arrow:before, .tb_st_slider_next_arrow:before {
    font-size: inherit;
    margin-left: -16px;
    margin-top: -16px;
    color: #e8e8e8;
}

.tb_st_multi_slide .slick-slide:not(.slick-active) { 
    margin: 50px 0; 
}

.tb_st_slider_container .slick-slide { 
    height: auto;
}

.tb_st_single_slide .tb_st_card_wrapper_00, .tb_st_single_slide .tb_st_card_wrapper{
    border-radius: 0px;
}

.slick-slide img.tb_st_img00 {
    display: block;
}

@keyframes tb_st_progress {
    0% { width: 0; }
    100% { width: 100%; }
}

@media screen and (max-width: 767px) {
.tb_st_multi_slide .slick-slide:not(.slick-active) {
    margin: 35px 0;
}

.tb_st_multi_slide .slick-active { 
    padding: 35px 0; 
}
}

.theme60_poweredByMain {
    padding-top: 50px;
    position: relative;
}
.theme60_poweredBy.poweredBy {
    right: 5px;
    top: 5px;
}

@media screen and (max-width: 480px) {
.tb_st_slider_prev_arrow {
    left: 0 !important;
    right: auto;
}
.tb_st_slider_next_arrow {
    left: auto;
    right: 0 !important;
}
.tb_st_card_wrapper {
    padding-bottom: 175%;
}
}